@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Poppins:wght@300;400;500;600&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #04052E;
    --color-bg-variant: #c2c5aa;
    --color-primary: #8f98f9;
    --color-primary-variant: #rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: #rgba(255, 255, 255, 0.6);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
    overflow-x: hiddern; /* has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;
}

::-webkit-scrollbar {
    display: none;   
}

body {
    font-family: 'Poppins', sans-serif; 
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

/* ========== GENERAL STYLES ========== */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}
h2 {
    font-size: 2.5rem;
}

h4 {
    font-size: 1.3rem;
}
h5 {
    font-size: 1.5rem;
}

section {
    height: -webkit-max-content;
    height: max-content;
    padding: 2rem 0 2rem 0;
}

.section_title::after {
    content: '';
    width: 400px;
    height: 1px;
    background: var(--color-primary);
    position: absolute;
    margin: auto;
    left: 0%;
    right: 0%;
}

.section_title{
    text-align: center;
    color: var(--color-light);
    padding-top: 2rem;
    padding-bottom: 2rem;
}

section > h2 {
    color: var(--color-primary);
    margin: 2rem 0 2rem 0;

}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
    font-weight: 700;
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 1rem 1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;

}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

.icons {
    color: var(--color-primary);
    margin-right: 15px;
  }

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px)  {
    .container {
        width: var(--container-width-md); 
    }

    section {
        margin-top: 4rem;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px)  {
    .container {
        width: var(--container-width-sm); 
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
header {
    height: 100vh;
    padding: 7rem 0 7rem 0;
    overflow: hidden;
}

.header__container {
    padding-top: 8%;
    text-align: center;
    height: 100%; 
    position: relative;
}

.cursor {
    color: var(--color-primary);
    height: 15px;
}

p{
    text-align: justify;
    padding: 1rem 5rem 1rem 5rem; /*top right bottom left*/
}

/* ========== CTA ========== */
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}



/* ========== HEADER SOCIALS ========== */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: -2rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary); 
}

/* ========== SCROLL DOWN ========== */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 1rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px)  {
    header {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;     
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px)  {
    header {
        height: 100vh;
        padding: 5rem 0 5rem 0;
        margin-bottom: -8rem;
    }
    .header__socials, .scroll__down {
        display: none;
    }
    p {
        font-size: 0.9rem; /* Adjust font size for smaller devices */
        padding: 1rem 0.5rem; /* Adjust padding for smaller devices */
    }
}
nav {
    background: rgba(0, 0, 0, 0.3) ;
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem; /*top/botoom and right/left*/
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
  padding-top: 2rem;
}

.about_description {
  text-align: justify;
  align-self: center;
  padding: 5rem 0 2rem 0;

}

/* ========== ME ========== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 0 0 0;
  justify-self: center; /* Center the element horizontally */
}

.me img {
  width: 100%; /* Ensure the image fills its container */
  height: auto; /* Maintain aspect ratio */
}

.technologies {
  padding: 1rem 0rem 1rem 0rem; /* top right bottom left */
}

ul {
  display: grid;
  grid-template-columns: repeat(2, minmax(150px, 10px));
  list-style-type: none;
  padding: 0rem 0rem 1rem 0rem;
}

.technologies_list li {
  display: flex;
  align-items: center;
}


/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
  .about__container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }

  .me {
    width: 50%; /* Set width to auto to prevent image cutoff */
   
  }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
  .about__container {
    display: grid; /* Ensure that the display remains grid for small devices */
    margin-top: 2rem;
  }

  .about_description {
    margin-bottom: 2rem;
  }
  
  .me {
    width: 40%;
    padding-bottom: 0;
    height: -webkit-min-content;
    height: min-content;
  }
}

.experience__container {
    padding: 1rem; /* Adjust padding for smaller devices */
}

.job_list {
    padding: 0;
    align-items: center; /* Remove padding for smaller devices */
}

.job_logo {
    width: 100%; /* Make the logo width 100% for smaller devices */
    max-width: 15rem; /* Set a maximum width for larger devices */
    margin: auto;
    padding: 1rem 0 2rem 0; /* Adjust padding for smaller devices */
}

.job_logo.bigger {
    max-width: 30rem; /* Set the desired maximum width for the larger logo */
}

.job_description {
    text-align: justify;
    display: flex;
    flex-direction: column;
    padding: 0; /* Remove padding for smaller devices *
}

.job_duration {
    padding: 0.5rem 0; /* Adjust padding for smaller devices */
}

.job_description li {
    position: relative;
    padding-bottom: 16px;
    padding-left: 10px; /* Adjust padding for smaller devices */
    padding-right: 10px; /* Adjust padding for smaller devices */
}

.slick-dots {
    position: absolute;
    bottom: 10px; /* Adjust as needed */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    list-style: none;
}

ul .slick-dots {
    padding: 2rem 0 2rem 0;
}
.slick-dots li {
    margin: 0 5px;
}

.slick-dots button {
    font-size: 12px; /* Adjust as needed */
    border: transparent;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    cursor: pointer;
    background-color: rgba(73, 72, 72, 0.3) !important; /* Gray out non-active dots */
    transition: background-color 0.3s;
}

.slick-dots button:hover {
    background-color: var(--color-primary) !important;
}

.slick-dots .slick-active button {
    background-color: var(--color-primary) !important; /* Change background color of active dot */
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .experience__container {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      grid-template-columns: 1fr;
      grid-gap: 0;
      gap: 0;
    }
  }
/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px)  {
    .experience__container {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .job_logo {
        display: none;
    }
  }
.project__container {
    display: grid;
    grid-gap: 1%;
    gap: 1%;
     /* Adjust the gap as needed */
}

.project_title {
    font-size: 1.8rem;
    position: relative;

}

.project_title::before {
    content: '>';
    color: var(--color-primary);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    right: calc(100% + 10px); /* Adjust the positioning as needed */
}

.project_description {
    text-align: justify;
    align-self: center;
    margin-bottom: 2rem; /* Increase margin to add space between projects */
}

.project__container img {
    width: 15rem;
    padding: 0rem 0 1rem 0; /* Adjust padding as needed */
    align-self: center; /* Center the image vertically */
}

.pokedex,
.metaintro,
.spacelab {
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap to the next line */
    justify-content: center; /* Center the items horizontally */
    grid-gap: 2%;
    gap: 2%;
}

.pokedex img,
.metaintro img {
    width: 20%;
}
.spacelab img {
    width: 45%; /* Adjust the width for 2 columns in medium and small devices */
}

#projects {
    margin-top: 2rem;
    margin-bottom: 15rem; /* Increase margin to make the section longer */
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .pokedex,
    .metaintro,
    .spacelab {
        display: flex;
        flex-wrap: wrap; /* Allow items to wrap to the next line */
        justify-content: center; /* Center the items horizontally */
        grid-gap: 2%;
        gap: 2%;
    }
    
    .pokedex img,
    .metaintro img {
        width: 40%; /* Adjust the width for 2 columns in medium and small devices */
    }
    
    .spacelab img {
        width: 100%; /* Spacelab remains in a single column */
    }
}
/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .project__container {
       padding-bottom: 30rem;
    }

    .project_title::before{
        top: 0%;
    }

    .project_header {
        padding-left: 5%;
    }

    .pokedex,
    .metaintro,
    .spacelab {
        flex-direction: column; /* Change to column layout for small devices */
        align-items: center; /* Center the items vertically for small devices */
    }
}

footer {
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 20rem;

}
.footer_copyright {
    margin-bottom: 4rem;
}



/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
   .footer_copyright {
    margin-top: 40rem;
   }
  }
  
  /* ========== MEDIA QUERIES (SMALL DEVICES) ========== */
  @media screen and (max-width: 600px) {
    .footer_copyright {
        margin-top: 20rem;
       }
  }
  
